@media only screen and (max-width: 576px) {
    .tag__list {
        padding-bottom: 48px
    }

    .tag__list .tag__item {
        margin-right: 8px;
        margin-bottom: 8px
    }

    .tag__list .tag__link {
        font-size: 14px
    }
}

.tag__info {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
    border-bottom: 1px solid var(--border-color-alt)
}

.tag__counter {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px 24px;
    border-radius: 16px;
    color: var(--heading-font-color);
    background: var(--background-alt-color)
}

.tag__counter span {
    font-family: "Josefin Sans", Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 32px;
    line-height: 1
}

.tag__counter small {
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px
}

@media only screen and (max-width: 576px) {
    .tag__counter span {
        font-size: 28px
    }
}

.tag__name {
    padding: 40px 0;
    margin-right: 40px;
    margin-bottom: 0;
    font-size: 40px;
    text-transform: capitalize
}

@media only screen and (max-width: 768px) {
    .tag__name {
        font-size: 32px
    }
}

@media only screen and (max-width: 576px) {
    .tag__name {
        margin-right: 28px;
        font-size: 26px
    }
}